@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/open-sans-v22-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-v22-latin_cyrillic-300.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/open-sans-v22-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v22-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/open-sans-v22-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-v22-latin_cyrillic-600.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/open-sans-v22-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v22-latin_cyrillic-700.woff") format("woff"); }

:root {
  --font-main: "Open Sans", "Arial", sans-serif;
  --accent-color: hsla(174 91% 45% / 1);
  --c-text-primary: hsla(0 0% 0% / 1);
  --c-nav-link: hsla(0 0% 100% / 0.7);
  --c-header: hsla(0 0% 100% / 1);
  --c-header-text: hsla(0 0% 100% / 0.8);
  --bg-nav-decor: hsla(0 0% 100% / 0.2);
  --bd-header-input: hsla(224 12% 81% / 1); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: middle; }

.page__wrapper {
  max-width: 1440px;
  margin: 0 auto; }

.page__a11y-hidden {
  position: fixed;
  -webkit-transform: scale(0);
          transform: scale(0); }

.page__body {
  margin: 0 auto;
  font-family: var(--font-main);
  font-size: 16px;
  font-weight: 400;
  color: var(--c-text-primary); }

.main-header {
  padding-top: 22px;
  padding-bottom: 240px;
  background: url("../img/promo/promo-bg-desktop@1x.jpg"); }

.main-header__wrapper {
  max-width: 880px;
  width: 100%;
  margin: 0 auto; }

.main-header__nav {
  position: relative;
  padding-bottom: 8px; }
  .main-header__nav::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: var(--bg-nav-decor); }

.main-header__nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0;
  padding: 0 22px;
  list-style-type: none; }

.main-header__nav a {
  font-weight: 300;
  line-height: 2;
  text-decoration: none;
  color: var(--c-nav-link); }

.main-header__content {
  display: grid;
  grid-template-columns: -webkit-max-content auto;
  grid-template-columns: max-content auto;
  -webkit-column-gap: 160px;
     -moz-column-gap: 160px;
          column-gap: 160px;
  margin-top: 256px; }

.main-header__text {
  max-width: 393px; }

.main-header h1 {
  margin-top: 28px;
  margin-bottom: 24px;
  font-size: 46px;
  font-weight: 700;
  line-height: 64px;
  color: var(--c-header); }

.main-header p {
  margin-bottom: 0;
  font-size: 20px;
  font-weight: 300;
  line-height: 32px;
  color: var(--c-header-text); }

.main-header__form {
  margin-top: 4px; }

.feedback-form {
  justify-self: end; }

.feedback-form fieldset {
  margin: 0;
  padding: 0;
  border: 0; }

.feedback-form legend {
  padding: 0;
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  text-align: center;
  color: var(--c-header); }

.feedback-form__inner-wrapper {
  display: grid;
  row-gap: 20px;
  margin-top: 20px; }

.feedback-form input {
  padding: 14px 30px 10px;
  width: 100%;
  font-family: var(--font-main);
  font-size: 15px;
  line-height: 24px;
  border: 0;
  -webkit-box-shadow: inset 0 0 0 1px var(--bd-header-input);
          box-shadow: inset 0 0 0 1px var(--bd-header-input);
  border-radius: 6px; }

.feedback-form [type="submit"] {
  padding: 15px 48px;
  font-size: 15px;
  font-weight: 600;
  line-height: 21px;
  color: var(--c-text-primary);
  border: 0;
  border-radius: 6px;
  background-color: var(--accent-color); }
